
.themer {
  position: fixed;
  top: 0;
  width: 50px;
  height: 50px;
  will-change: width, height, transform;
  -webkit-transition: width 1s, height 1s, -webkit-transform 0.3s linear;
  transition: width 1s, height 1s, -webkit-transform 0.3s linear;
  transition: width 1s, height 1s, transform 0.3s linear;
  transition: width 1s, height 1s, transform 0.3s linear, -webkit-transform 0.3s linear;
  z-index: 1000;
  -webkit-transform-origin: center center;
          transform-origin: center center;
}
.themer > svg {
    position: absolute;
    cursor: pointer;
    width: 100%;
    height: 100%;
}
.themer > svg circle {
      -webkit-transition: all 1s;
      transition: all 1s;
      cx: 50;
      cy: 50;
      -webkit-animation: pulse 3s ease-out infinite normal;
              animation: pulse 3s ease-out infinite normal;
      -webkit-transform-origin: center center;
              transform-origin: center center;
}
.themer > svg circle:nth-child(1) {
        opacity: 0.7;
}
.themer > svg circle:nth-child(2) {
        -webkit-transform: scale(0.8);
                transform: scale(0.8);
}
.themer:nth-child(1) {
    left: 0;
}
.themer:nth-child(2) {
    right: 0;
}
.themer.theme-active svg {
    -webkit-animation: activate 1s forwards;
            animation: activate 1s forwards;
}
@media screen and (min-width: 600px) {
.themer {
    width: 80px;
    height: 80px;
    bottom: 0;
    top: auto;
}
.themer:not(.theme-active):hover svg {
    -webkit-animation: hover 1s;
            animation: hover 1s;
}
.themer:not(.theme-active):hover circle {
    -webkit-animation-duration: 5s;
            animation-duration: 5s;
}
.themer:not(.theme-active):hover circle:nth-child(1) {
      -webkit-animation-delay: 0.14s;
              animation-delay: 0.14s;
}
}
@-webkit-keyframes hover {
0% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
10% {
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
}
40% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}
90% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
}
@keyframes hover {
0% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
10% {
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
}
40% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}
90% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
}
@-webkit-keyframes pulse {
0% {
    r: 40%;
}
25% {
    r: 50%;
}
60% {
    r: 45%;
}
100% {
    r: 40%;
}
}
@keyframes pulse {
0% {
    r: 40%;
}
25% {
    r: 50%;
}
60% {
    r: 45%;
}
100% {
    r: 40%;
}
}
@-webkit-keyframes activate {
0% {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
}
60% {
    opacity: 0.6;
}
99% {
    -webkit-transform: scale(50);
            transform: scale(50);
}
100% {
    opacity: 0;
    -webkit-transform: scale(0);
            transform: scale(0);
}
}
@keyframes activate {
0% {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
}
60% {
    opacity: 0.6;
}
99% {
    -webkit-transform: scale(50);
            transform: scale(50);
}
100% {
    opacity: 0;
    -webkit-transform: scale(0);
            transform: scale(0);
}
}



