
html,
body {
  font-size: 10px;
  font-family: 'Quicksand', sans-serif;
}
body {
  padding: 0;
  margin: 0;
}
body * {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
h2,
h3,
h4 {
  margin: 10px 0;
}
h2 {
  font-size: 4.2rem;
}
h3 {
  font-size: 2.8rem;
}
h4 {
  margin: 0;
  font-size: 1.8rem;
}
#app {
  height: 100vh;
  width: 100vw;
  max-height: 100%;
  overflow-y: auto;
  overflow-y: overlay;
  -webkit-transition: background-color 0.3s 0.2s;
  transition: background-color 0.3s 0.2s;
  background-color: var(--main);
  position: relative;
}
#app::-webkit-scrollbar {
    display: none;
}
.main {
  position: relative;
  top: 15%;
  max-width: 900px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  -webkit-transition: -webkit-filter 1s;
  transition: -webkit-filter 1s;
  transition: filter 1s;
  transition: filter 1s, -webkit-filter 1s;
  border-radius: var(--b-r);
}
.main > section {
    position: relative;
}
.main * {
    -webkit-perspective: 300px;
            perspective: 300px;
}
@-webkit-keyframes show-main {
0% {
    border-radius: 0px;
    background-color: var(--main);
    -webkit-transform: scale(0.99);
            transform: scale(0.99);
    opacity: 0;
}
20% {
    opacity: 1;
}
60% {
    -webkit-transform: scale(1.01);
            transform: scale(1.01);
    border-radius: 16px;
}
80% {
    background-color: var(--main);
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    border-radius: 4px;
    background-color: var(--main-1);
    opacity: 1;
}
}
@keyframes show-main {
0% {
    border-radius: 0px;
    background-color: var(--main);
    -webkit-transform: scale(0.99);
            transform: scale(0.99);
    opacity: 0;
}
20% {
    opacity: 1;
}
60% {
    -webkit-transform: scale(1.01);
            transform: scale(1.01);
    border-radius: 16px;
}
80% {
    background-color: var(--main);
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    border-radius: 4px;
    background-color: var(--main-1);
    opacity: 1;
}
}
@-webkit-keyframes hello-after {
from {
    opacity: 0;
}
to {
    opacity: 1;
}
}
@keyframes hello-after {
from {
    opacity: 0;
}
to {
    opacity: 1;
}
}
@-webkit-keyframes hello {
from {
    background-color: var(--dark);
}
to {
    background-color: var(--dark-1);
}
}
@keyframes hello {
from {
    background-color: var(--dark);
}
to {
    background-color: var(--dark-1);
}
}
@media screen and (max-width: 600px) {
html {
    font-size: 8px;
}
.main {
    top: 12%;
    padding-bottom: 50px;
}
}



