
.card-empty {
  width: 100%;
  color: var(--text-color);
  font-size: 4rem;
  position: relative;
  margin-bottom: 50px;
  border-radius: var(--b-r);
}
.card-empty video {
    border-radius: var(--b-r);
    width: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    opacity: 0.3;
    -webkit-box-shadow: var(--block-shadow);
            box-shadow: var(--block-shadow);
}
.card-empty h2 {
    position: absolute;
    top: 2rem;
    left: 3rem;
}
.card-promise {
  width: 80%;
  padding: 20px;
  font-size: 3.2rem;
  border-radius: var(--b-r);
  -webkit-transform: translateY(2rem);
          transform: translateY(2rem);
  background-color: var(--main);
  position: absolute;
  bottom: 0;
  left: 10%;
}
.card-promise a {
    color: var(--text-color);
}
.tumbleweed {
  width: 100px;
  height: 100px;
  position: fixed;
  will-change: bottom, left, transform;
  -webkit-animation: jumping 1.5s infinite, rolling 8s linear infinite, rotating reverse 1s linear infinite;
          animation: jumping 1.5s infinite, rolling 8s linear infinite, rotating reverse 1s linear infinite;
}
.showcase-enter {
  -webkit-transform: translateY(100px);
          transform: translateY(100px);
  opacity: 0;
}
.showcase-enter .card-promise {
    -webkit-transform: translateY(300px);
            transform: translateY(300px);
}
.showcase-enter-active {
  -webkit-transition: opacity 0.4s, -webkit-transform 1s ease;
  transition: opacity 0.4s, -webkit-transform 1s ease;
  transition: transform 1s ease, opacity 0.4s;
  transition: transform 1s ease, opacity 0.4s, -webkit-transform 1s ease;
}
.showcase-enter-active .card-promise {
    -webkit-transition: -webkit-transform 1s;
    transition: -webkit-transform 1s;
    transition: transform 1s;
    transition: transform 1s, -webkit-transform 1s;
}
@media screen and (max-width: 600px) {
.card-empty {
    width: 98%;
    left: 1%;
}
.card-promise {
    position: relative;
    width: 100%;
    left: auto;
    bottom: auto;
}
}
@-webkit-keyframes jumping {
0% {
    bottom: -100px;
    -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
}
25% {
    bottom: -120px;
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
}
50% {
    bottom: -110px;
    -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
}
75% {
    bottom: -130px;
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
}
100% {
    bottom: -100px;
    -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
}
}
@keyframes jumping {
0% {
    bottom: -100px;
    -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
}
25% {
    bottom: -120px;
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
}
50% {
    bottom: -110px;
    -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
}
75% {
    bottom: -130px;
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
}
100% {
    bottom: -100px;
    -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
}
}
@-webkit-keyframes rolling {
0% {
    left: calc(100% + 500px);
}
100% {
    left: -500px;
}
}
@keyframes rolling {
0% {
    left: calc(100% + 500px);
}
100% {
    left: -500px;
}
}
@-webkit-keyframes rotating {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@keyframes rotating {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}



