
.page-main {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-flow: column nowrap;
          flex-flow: column nowrap;
  min-height: 500px;
  background-color: transparent;
}
.page-main > * {
    display: block;
    width: 100%;
    position: relative;
}
.main-enter-active {
  -webkit-transition: opacity .8s;
  transition: opacity .8s;
}
.main-enter-active .heading,
  .main-enter-active .social,
  .main-enter-active .desc {
    -webkit-transition: opacity 0.6s, -webkit-transform 1s;
    transition: opacity 0.6s, -webkit-transform 1s;
    transition: transform 1s, opacity 0.6s;
    transition: transform 1s, opacity 0.6s, -webkit-transform 1s;
}
.main-enter .heading {
  -webkit-transform: translateY(-30px);
          transform: translateY(-30px);
}
.main-enter .desc {
  opacity: 0;
}
.main-enter .social {
  -webkit-transform: translateY(40px);
          transform: translateY(40px);
}
.about-leave-active,
.showcase-leave-active,
.main-leave-active {
  -webkit-transition: opacity 0.5s, -webkit-transform 0.5s;
  transition: opacity 0.5s, -webkit-transform 0.5s;
  transition: transform 0.5s, opacity 0.5s;
  transition: transform 0.5s, opacity 0.5s, -webkit-transform 0.5s;
}
.about-leave-active .heading,
  .about-leave-active .social,
  .about-leave-active .card-empty,
  .about-leave-active .card-promise,
  .showcase-leave-active .heading,
  .showcase-leave-active .social,
  .showcase-leave-active .card-empty,
  .showcase-leave-active .card-promise,
  .main-leave-active .heading,
  .main-leave-active .social,
  .main-leave-active .card-empty,
  .main-leave-active .card-promise {
    -webkit-transition: -webkit-transform .4s;
    transition: -webkit-transform .4s;
    transition: transform .4s;
    transition: transform .4s, -webkit-transform .4s;
}
.about-leave-active.about,
  .showcase-leave-active.about,
  .main-leave-active.about {
    -webkit-transition: opacity 0.8s;
    transition: opacity 0.8s;
}
.about-leave-active .about-main,
  .about-leave-active .about-main ~ *,
  .showcase-leave-active .about-main,
  .showcase-leave-active .about-main ~ *,
  .main-leave-active .about-main,
  .main-leave-active .about-main ~ * {
    -webkit-transition: opacity 0.6s, -webkit-transform 1s;
    transition: opacity 0.6s, -webkit-transform 1s;
    transition: opacity 0.6s, transform 1s;
    transition: opacity 0.6s, transform 1s, -webkit-transform 1s;
}
.showcase-leave-to,
.about-leave-to,
.main-leave-to {
  opacity: 0;
}
.showcase-leave-to .heading,
  .about-leave-to .heading,
  .main-leave-to .heading {
    -webkit-transform: translateY(-30px);
            transform: translateY(-30px);
}
.showcase-leave-to .social,
  .about-leave-to .social,
  .main-leave-to .social {
    -webkit-transform: translateY(40px);
            transform: translateY(40px);
}
.showcase-leave-to .card-empty,
  .about-leave-to .card-empty,
  .main-leave-to .card-empty {
    -webkit-transform: translateY(-100px);
            transform: translateY(-100px);
}
.showcase-leave-to .card-promise,
  .about-leave-to .card-promise,
  .main-leave-to .card-promise {
    -webkit-transform: translateY(200px);
            transform: translateY(200px);
}
.showcase-leave-to .about-main,
  .about-leave-to .about-main,
  .main-leave-to .about-main {
    opacity: 0;
    -webkit-transform: translate3d(0, -350px, -100px);
            transform: translate3d(0, -350px, -100px);
}
.showcase-leave-to .about-main ~ *,
    .about-leave-to .about-main ~ *,
    .main-leave-to .about-main ~ * {
      opacity: 0;
      -webkit-transform: translateY(150px);
              transform: translateY(150px);
}
.heading {
  font-size: 7rem;
  margin: 10% 4rem 0 0;
  text-align: right;
  -webkit-transition: color 0.4s;
  transition: color 0.4s;
  color: var(--accent);
  font-family: 'Dosis', sans-serif;
}
.heading > span {
    opacity: 0;
    -webkit-animation: fadeIn 3s forwards;
            animation: fadeIn 3s forwards;
}
.desc {
  font-size: 3rem;
  text-align: right;
  padding: 0;
  margin: 0 4rem 0 0;
  margin-top: 1rem;
  color: var(--text-color);
}
.social {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-flow: row nowrap;
          flex-flow: row nowrap;
  position: absolute;
  bottom: 0;
}
.link {
  position: relative;
  padding: 10px;
  margin: 10px;
  display: inline-block;
  width: 50px;
  height: 50px;
}
.link:hover svg {
  fill: var(--accent);
}
.link svg {
  width: 100%;
  height: 100%;
  fill: var(--text-color);
  -webkit-transition: fill 0.4s;
  transition: fill 0.4s;
}
@-webkit-keyframes fadeIn {
from {
    opacity: 0;
}
to {
    opacity: 1;
}
}
@keyframes fadeIn {
from {
    opacity: 0;
}
to {
    opacity: 1;
}
}
@media screen and (max-width: 760px) {
.heading, .desc {
    margin-right: 5px;
}
.heading {
    font-size: 5rem;
}
.desc {
    font-size: 2.4rem;
}
}
@media screen and (max-width: 600px) {
.page-main {
    min-height: 400px;
}
.container {
    padding-right: 10px;
}
}



