
.exp-timeline {
  margin: 0;
  padding: 0;
  list-style: none;
  position: relative;
  /*=== Timeline ===*/
}
.exp-timeline:before {
    position: absolute;
    top: 10px;
    bottom: -12px;
    left: 90px;
    width: 2px;
    border-radius: 1px;
    -webkit-box-shadow: var(--block-shadow);
            box-shadow: var(--block-shadow);
    background-color: var(--text-color);
    content: '';
    opacity: 0.6;
}
.exp-job {
  padding-left: 100px;
  position: absolute;
  display: block;
  width: 100%;
  -webkit-filter: brightness(50%);
          filter: brightness(50%);
}
.exp-job:last-child {
    -webkit-filter: brightness(100%);
            filter: brightness(100%);
}
.exp-job:last-child .exp-job-time:after {
      -webkit-box-shadow: var(--block-shadow);
              box-shadow: var(--block-shadow);
}
.exp-job-time {
    position: absolute;
    width: 80px;
    left: 0;
    font-size: 16px;
    top: 10px;
    background-color: var(--text-color);
    color: var(--main);
    border-radius: var(--b-r);
    font-weight: 700;
    -webkit-box-shadow: var(--block-shadow);
            box-shadow: var(--block-shadow);
    padding: 0 4px;
}
.exp-job-time:after {
      position: absolute;
      right: -16px;
      width: 10px;
      height: 10px;
      border-radius: 50%;
      background-color: var(--text-color);
      content: '';
      top: 5px;
      -webkit-transform: rotate(45deg);
              transform: rotate(45deg);
}
.exp-job-main {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-flow: row nowrap;
            flex-flow: row nowrap;
    -webkit-box-pack: start;
        -ms-flex-pack: start;
            justify-content: flex-start;
    height: 100%;
    padding: 10px 0;
    line-height: 1;
}
@media screen and (max-width: 420px) {
.exp-job-main h4 {
    font-size: 2.6rem;
}
.exp-job-main h4 span {
      display: none;
}
}



