
.about-who {
  position: relative;
  margin-bottom: 20px;
  padding: 20px;
  border-radius: var(--b-r);
}
.about-who > h2 {
    position: relative;
    margin-bottom: 3rem;
}
.about-who > img {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    border-radius: var(--b-r);
    -webkit-filter: var(--who-fitler);
            filter: var(--who-fitler);
    opacity: 1;
    -webkit-transition: opacity 0.6s 0.2s ease, -webkit-filter 0.6s ease, -webkit-clip-path 0.8s 0s ease;
    transition: opacity 0.6s 0.2s ease, -webkit-filter 0.6s ease, -webkit-clip-path 0.8s 0s ease;
    transition: filter 0.6s ease, opacity 0.6s 0.2s ease, clip-path 0.8s 0s ease;
    transition: filter 0.6s ease, opacity 0.6s 0.2s ease, clip-path 0.8s 0s ease, -webkit-filter 0.6s ease, -webkit-clip-path 0.8s 0s ease;
    z-index: 0;
}
.about-who > img:nth-child(1) {
      -webkit-clip-path: inset(0 0 0 100%);
              clip-path: inset(0 0 0 100%);
}
.about-who > img:nth-child(2) {
      -webkit-clip-path: inset(0 100% 0 0);
              clip-path: inset(0 100% 0 0);
}
.about-who > img.who-img-active {
      z-index: -1;
      -webkit-transition: none;
      transition: none;
      opacity: 1;
      -webkit-clip-path: inset(0 0 0 0);
              clip-path: inset(0 0 0 0);
}
.who-container {
  height: 400px;
}
.who-back {
  position: absolute;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-flow: row nowrap;
          flex-flow: row nowrap;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  height: 100%;
  width: 100%;
  color: var(--text-color);
  text-align: center;
}
.who-back h3 {
    cursor: pointer;
    text-align: center;
    display: inline-block;
    border-bottom: 2px solid var(--text-color);
    padding: 8px 8px;
    opacity: 0;
    -webkit-transition: opacity 0.2s ease;
    transition: opacity 0.2s ease;
}
.who-back .back-active {
    opacity: 1;
    -webkit-transition-delay: 0.3s;
            transition-delay: 0.3s;
}
.who-inner {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-flow: row nowrap;
          flex-flow: row nowrap;
  height: 100%;
  width: 100%;
  -webkit-transition: -webkit-clip-path 0.8s ease, -webkit-transform 0.8s ease;
  transition: -webkit-clip-path 0.8s ease, -webkit-transform 0.8s ease;
  transition: clip-path 0.8s ease, transform 0.8s ease;
  transition: clip-path 0.8s ease, transform 0.8s ease, -webkit-clip-path 0.8s ease, -webkit-transform 0.8s ease;
  background-color: var(--who-inner-bg);
}
.who-inner a {
    color: var(--text-color);
}
.who-inner-0 {
    -webkit-clip-path: inset(0px 52% 0px 2% round 2px);
            clip-path: inset(0px 52% 0px 2% round 2px);
}
.who-inner-1 {
    -webkit-clip-path: inset(0px 2% 0px 52% round 2px);
            clip-path: inset(0px 2% 0px 52% round 2px);
}
.who-block {
  width: 45%;
  height: 100%;
  padding: 15px;
}
.who-block h3 {
    font-size: 2.2rem;
}
.who-block h4 {
    font-size: 1.8rem;
}
.who-block p {
    font-size: 1.6rem;
}
@-webkit-keyframes fadeInImg {
from {
    -webkit-transform: translate3d(-15px, -15px, -15px);
            transform: translate3d(-15px, -15px, -15px);
    opacity: 0;
}
to {
    -webkit-transform: none;
            transform: none;
    opacity: 1;
}
}
@keyframes fadeInImg {
from {
    -webkit-transform: translate3d(-15px, -15px, -15px);
            transform: translate3d(-15px, -15px, -15px);
    opacity: 0;
}
to {
    -webkit-transform: none;
            transform: none;
    opacity: 1;
}
}
@-webkit-keyframes backImg {
from {
    -webkit-transform: translateX(0, 0, 0);
            transform: translateX(0, 0, 0);
}
to {
    -webkit-transform: translate3d(50px, 20px 0);
            transform: translate3d(50px, 20px 0);
}
}
@keyframes backImg {
from {
    -webkit-transform: translateX(0, 0, 0);
            transform: translateX(0, 0, 0);
}
to {
    -webkit-transform: translate3d(50px, 20px 0);
            transform: translate3d(50px, 20px 0);
}
}
@-webkit-keyframes switchBlock {
0% {
    -webkit-clip-path: inset(0 52% 0 2% round 2px);
            clip-path: inset(0 52% 0 2% round 2px);
}
50% {
    -webkit-clip-path: inset(44% 48% 44% 48% round 2px);
            clip-path: inset(44% 48% 44% 48% round 2px);
}
100% {
    -webkit-clip-path: inset(0 2% 0 52% round 2px);
            clip-path: inset(0 2% 0 52% round 2px);
}
}
@keyframes switchBlock {
0% {
    -webkit-clip-path: inset(0 52% 0 2% round 2px);
            clip-path: inset(0 52% 0 2% round 2px);
}
50% {
    -webkit-clip-path: inset(44% 48% 44% 48% round 2px);
            clip-path: inset(44% 48% 44% 48% round 2px);
}
100% {
    -webkit-clip-path: inset(0 2% 0 52% round 2px);
            clip-path: inset(0 2% 0 52% round 2px);
}
}
@media screen and (max-width: 600px) {
.about-who {
    overflow: hidden;
    margin-bottom: 1rem;
    padding-bottom: 0;
}
.about-who > img {
      width: auto;
}
.about-who .who-container {
      width: calc(100% + 8rem);
      margin: 0 -3rem;
      height: auto;
      overflow: auto;
}
.about-who .who-block {
      padding: 1rem;
      width: calc(100vw - 4rem);
}
.about-who .who-block h3 {
        font-size: 3.5rem;
}
.about-who .who-block h4 {
        font-size: 2.4rem;
}
.about-who .who-block p {
        font-size: 2.4rem;
}
.about-who .who-back {
      display: none;
}
.about-who .who-inner {
      width: 200vw;
}
.about-who .who-inner-0 {
        -webkit-clip-path: none;
                clip-path: none;
}
.about-who .who-inner-1 {
        -webkit-clip-path: none;
                clip-path: none;
}
}



