
.nav {
  background-color: var(--nav-bg, transparent);
  width: 100%;
  height: 50px;
  position: fixed;
  left: 0;
  z-index: 100;
  border-top-left-radius: 4px;
  overflow: hidden;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.nav-appear .nav-link {
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
    opacity: 0;
    -webkit-animation: showNavLinks 1.2s ease-out;
            animation: showNavLinks 1.2s ease-out;
}
.nav ul {
    list-style: none;
    padding: 0;
    margin: 0;
}
.nav .nav-link {
    display: inline-block;
    height: 50px;
    margin: 0 5px;
    padding: 5px;
    -webkit-transition: background-color 0.3s, opacity 0.3s, -webkit-backdrop-filter 0.5s;
    transition: background-color 0.3s, opacity 0.3s, -webkit-backdrop-filter 0.5s;
    transition: backdrop-filter 0.5s, background-color 0.3s, opacity 0.3s;
    transition: backdrop-filter 0.5s, background-color 0.3s, opacity 0.3s, -webkit-backdrop-filter 0.5s;
    opacity: 0.8;
    -webkit-backdrop-filter: blur(4px);
            backdrop-filter: blur(4px);
}
.nav .nav-link:hover {
      opacity: 1;
      background-color: var(--nav-link-hover-bg, transparent);
}
.nav .nav-link a {
      display: block;
      width: 100%;
      height: 100%;
      -webkit-transition: color 0.3s;
      transition: color 0.3s;
      color: var(--text-color);
      text-align: center;
      text-decoration: none;
      font-size: 18px;
      line-height: 38px;
      padding: 0 10px;
}
.nav .nav-link.router-link-exact-active {
      opacity: 1;
}
.nav .nav-link.router-link-exact-active a {
        color: var(--accent);
}
.nav .pointer {
    display: block;
    height: 2px;
    width: 10px;
    position: absolute;
    bottom: 0;
    left: 0;
    -webkit-transition: -webkit-transform 0.6s ease;
    transition: -webkit-transform 0.6s ease;
    transition: transform 0.6s ease;
    transition: transform 0.6s ease, -webkit-transform 0.6s ease;
    background-color: var(--accent);
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
    -webkit-transform-origin: left;
            transform-origin: left;
}
@-webkit-keyframes showNavLinks {
0% {
    -webkit-transform: translateY(-40px);
            transform: translateY(-40px);
    opacity: 0;
}
40% {
    opacity: 1;
}
70% {
    -webkit-transform: translateY(5px);
            transform: translateY(5px);
    opacity: 1;
}
100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
}
}
@keyframes showNavLinks {
0% {
    -webkit-transform: translateY(-40px);
            transform: translateY(-40px);
    opacity: 0;
}
40% {
    opacity: 1;
}
70% {
    -webkit-transform: translateY(5px);
            transform: translateY(5px);
    opacity: 1;
}
100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
}
}
@media screen and (max-width: 600px) {
.nav {
    --bg: var(--nav-bg, var(--main));
    top: auto;
    bottom: 0;
    width: 100%;
    background-color: var(--bg);
}
.nav .pointer {
      bottom: auto;
      top: 0;
}
}



