
.about {
  color: var(--text-color);
  min-height: 600px;
  padding: 0 20px;
}
.about-enter {
    opacity: 0;
    background-color: transparent;
}
.about-enter .about-img img {
      -webkit-clip-path: circle(0% at 10% 10%);
              clip-path: circle(0% at 10% 10%);
}
.about-enter .about-info {
      -webkit-transform: translateX(120px);
              transform: translateX(120px);
}
.about-enter .about-who {
      -webkit-transform: translateY(300px);
              transform: translateY(300px);
}
.about-enter-active {
    -webkit-transition: background-color 0.7s, opacity 0.3s, -webkit-clip-path 0.9s;
    transition: background-color 0.7s, opacity 0.3s, -webkit-clip-path 0.9s;
    transition: background-color 0.7s, opacity 0.3s, clip-path 0.9s;
    transition: background-color 0.7s, opacity 0.3s, clip-path 0.9s, -webkit-clip-path 0.9s;
}
.about-enter-active .about-img img {
      -webkit-transition: -webkit-clip-path 0.9s;
      transition: -webkit-clip-path 0.9s;
      transition: clip-path 0.9s;
      transition: clip-path 0.9s, -webkit-clip-path 0.9s;
}
.about-enter-active .about-info {
      -webkit-transition: -webkit-transform 0.9s;
      transition: -webkit-transform 0.9s;
      transition: transform 0.9s;
      transition: transform 0.9s, -webkit-transform 0.9s;
}
.about-enter-active .about-who {
      -webkit-transition: -webkit-transform 0.9s;
      transition: -webkit-transform 0.9s;
      transition: transform 0.9s;
      transition: transform 0.9s, -webkit-transform 0.9s;
}
.about-main {
    position: relative;
    z-index: 100;
    padding: 20px 0;
    margin-left: 20px;
    contain: style;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    -webkit-transition: -webkit-perspective-origin 0.2s linear;
    transition: -webkit-perspective-origin 0.2s linear;
    transition: perspective-origin 0.2s linear;
    transition: perspective-origin 0.2s linear, -webkit-perspective-origin 0.2s linear;
}
.about-main:hover .about-img {
      -webkit-transition: -webkit-transform 1s;
      transition: -webkit-transform 1s;
      transition: transform 1s;
      transition: transform 1s, -webkit-transform 1s;
      -webkit-transform: translate3d(-15px, -25px, -20px);
              transform: translate3d(-15px, -25px, -20px);
}
.about-main:hover .about-img img {
        -webkit-transition: -webkit-filter 1s;
        transition: -webkit-filter 1s;
        transition: filter 1s;
        transition: filter 1s, -webkit-filter 1s;
        -webkit-filter: var(--about-img-filter);
                filter: var(--about-img-filter);
}
.about-main:hover .about-info {
      -webkit-transition: -webkit-transform 1s;
      transition: -webkit-transform 1s;
      transition: transform 1s;
      transition: transform 1s, -webkit-transform 1s;
      -webkit-transform: translate3d(-120px, 20px, 30px);
              transform: translate3d(-120px, 20px, 30px);
}
.about .about-info h5 {
    font-size: 2.4rem;
    margin: 0.6rem 0;
    white-space: nowrap;
}
.about-img {
    height: 230px;
    border-top-left-radius: 4px;
    border-bottom-left-radius: 4px;
    overflow: hidden;
    -webkit-transform: translate3d(0, -20px, 30px);
            transform: translate3d(0, -20px, 30px);
    -webkit-transition: -webkit-transform 1.6s, -webkit-filter 0.6s;
    transition: -webkit-transform 1.6s, -webkit-filter 0.6s;
    transition: transform 1.6s, filter 0.6s;
    transition: transform 1.6s, filter 0.6s, -webkit-transform 1.6s, -webkit-filter 0.6s;
    border-radius: var(--b-r);
    -webkit-filter: drop-shadow(var(--about-img-shadow));
            filter: drop-shadow(var(--about-img-shadow));
}
.about-img img {
      max-height: 100%;
      -webkit-transition: -webkit-clip-path 0.6s, -webkit-filter 0.6s;
      transition: -webkit-clip-path 0.6s, -webkit-filter 0.6s;
      transition: clip-path 0.6s, filter 0.6s;
      transition: clip-path 0.6s, filter 0.6s, -webkit-clip-path 0.6s, -webkit-filter 0.6s;
      -webkit-clip-path: circle(100% at 10% 50%);
              clip-path: circle(100% at 10% 50%);
      -webkit-transform: translateZ(10px);
              transform: translateZ(10px);
}
.about-info {
    position: absolute;
    top: 15%;
    left: 50%;
    width: auto;
    display: inline-block;
    -webkit-transform: translateX(-15px);
            transform: translateX(-15px);
    -webkit-transition: opacity 0.4s, -webkit-transform 1.6s ease-out;
    transition: opacity 0.4s, -webkit-transform 1.6s ease-out;
    transition: transform 1.6s ease-out, opacity 0.4s;
    transition: transform 1.6s ease-out, opacity 0.4s, -webkit-transform 1.6s ease-out;
}
.about-info p {
      text-align: right;
      font-size: 1.5rem;
}
.about-block {
  padding: 20px 20px 10px;
  background-color: var(--main);
  border-radius: var(--b-r);
  -webkit-box-shadow: var(--block-shadow);
          box-shadow: var(--block-shadow);
  margin-bottom: 20px;
}
@media screen and (max-width: 600px) {
.about {
    padding: 0 1rem;
    -webkit-perspective: 2000px;
            perspective: 2000px;
}
.about .about-main {
      padding: 0;
      pointer-events: none;
      margin: 0;
}
.about .about-img {
      height: auto;
      -webkit-transition: all 1s;
      transition: all 1s;
      -webkit-transform: translate3d(0, -10px, -40px);
              transform: translate3d(0, -10px, -40px);
}
.about .about-img:hover {
        -webkit-transition: none;
        transition: none;
}
.about .about-img img {
        width: 100%;
        -webkit-filter: var(--about-img-filter);
                filter: var(--about-img-filter);
        -webkit-clip-path: none;
                clip-path: none;
}
.about .about-img img:hover {
          -webkit-transition: none;
          transition: none;
}
.about .about-info {
      top: 0;
      left: 0;
      padding: 0.5rem;
      -webkit-transition: all 1s;
      transition: all 1s;
      -webkit-transform: translate3d(0, 0, 10px);
              transform: translate3d(0, 0, 10px);
}
.about .about-info:hover {
        -webkit-transition: none;
        transition: none;
}
.about .about-info h2 {
        font-size: 3.5rem;
}
.about .about-info h5 {
        font-size: 2.2rem;
}
.about .about-info h5,
      .about .about-info p {
        white-space: normal;
}
}



